/**
 *  <code>shaderVariable</code> shader
 */
export class ShaderVariable {
	/**@internal */
	name: string;
	/**@internal */
	type: number;
	/**@internal */
	location: WebGLUniformLocation;
	/**@internal */
	isArray: boolean;
	/**@internal */
	textureID: number;
	/**@internal */
	dataOffset: number;

	/**@internal */
	caller: any;
	/**@internal */
	fun: any;
	/**@internal */
	uploadedValue: any[];

	/**
	 *  <code>shaderVariable</code> 
	 */
	constructor() {

		this.textureID = -1;
	}

}


